body {
  font-family: 'TTLakes', sans-serif; }

.block2 {
  width: 100%;
  margin: 0; }

.upperHed {
  display: flex;
  height: 56px;
  background-image: linear-gradient(to right, #f8f8f8, white);
  border-top: 1px solid #B8FFEC;
  margin-top: 16px; }

.upperHed-text {
  font-size: 16px;
  font-weight: normal;
  text-transform: uppercase;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 16px;
  color: #7e7e82; }

.cats,
.remonts {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 8px; }

.cat,
.remont {
  display: flex;
  height: 72px;
  min-width: 224px;
  margin: 0;
  justify-content: space-between;
  background-color: white;
  border-radius: 5px;
  border: 1px solid #EAEAEA; }

.remont {
  height: 160px; }

.swiper-slide__cat,
.swiper-slide__remont {
  background-color: #F8F8F8;
  margin: 16px 0 16px 16px;
  width: auto; }

.bitmap {
  margin-left: 16px;
  margin-top: auto;
  margin-bottom: auto; }

.remont .bitmap {
  margin-left: 16px;
  margin-top: 16px;
  margin-bottom: auto; }

.line__go {
  height: 40px;
  width: 40px;
  margin-right: 16px;
  margin-top: auto;
  margin-bottom: auto; }

.remont .line__go {
  margin-right: 16px;
  margin-top: auto;
  margin-bottom: 16px;
  margin-left: auto; }

.footerMenu {
  display: flex;
  height: 24px;
  width: 134px;
  margin-left: 16px;
  margin-bottom: 34px;
  justify-content: space-between; }

.line__expand,
.remonts-line__expand {
  height: 24px;
  width: 24px;
  margin-top: auto;
  margin-bottom: auto; }

.line_text,
.remonts-line_text {
  height: 16px;
  font-size: 16px;
  font-weight: normal;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 9px; }

.line_input,
.slaid,
.remonts-line_input {
  display: none; }

/*screen*/
@media screen and (max-width: 768px) {
  .slaid {
    display: block; }
  .cats,
  .remonts,
  .footerMenu {
    display: none; }
  .cats,
  .remonts {
    flex-wrap: nowrap; } }

@media screen and (min-width: 768px) {
  .cat,
  .remont {
    margin: 8px; } }
