@charset "UTF-8";
@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: medium;
  src: local("TTLakes"), url(./fonts/TTLakes-Medium.woff) format("woff"); }

@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: normal;
  src: local("TTLakes"), url(./fonts/TTLakes-Regular.woff) format("woff"); }

/* Жирный */
@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: bold;
  src: local("TTLakes"), url(./fonts/TTLakes-Bold.woff) format("woff"); }

body {
  font-family: 'TTLakes', sans-serif;
  width: 100%;
  margin: 0; }

.block1 {
  width: 100%;
  margin: 0; }

a {
  margin: 0;
  padding: 0;
  font-size: 0; }

.line,
.main-line,
.line__item {
  display: flex;
  width: auto;
  justify-content: flex-end; }

.main-line__repair {
  margin-right: 32px; }

.main-line__checkstatus {
  margin-right: 30px; }

/*line*/
.line {
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 0 7px; }

.line__item {
  justify-content: space-around;
  align-items: center; }

.line__group {
  margin: 0;
  width: 90px; }

.line__der__burger,
.line__der {
  border: 1px solid #e3e3ec;
  border-radius: 50%;
  height: 26px;
  margin-left: 16px; }

.line__group,
.line__repair,
.line__checkstatus {
  margin-left: 16px; }

/*line*/
/*main */
.tittle {
  display: block; }

h1 {
  font-family: 'TTLakes', sans-serif;
  font-weight: bold;
  height: 40px;
  margin-bottom: 24px;
  margin-left: -5px;
  font-size: 28px; }

h1::before {
  content: ".";
  margin-left: 0;
  margin-right: 10px;
  color: #B8FFEC;
  background-color: #B8FFEC; }

main {
  background-color: #F8F8F8; }

.main-line a {
  display: flex;
  font-size: 18px;
  padding: 5px 10px;
  margin: 5px;
  text-decoration: none;
  color: #1b1c21;
  height: 24px;
  align-self: center; }

.main-line img {
  margin-left: 10px;
  align-self: center; }

.nav-list {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: nowrap;
  flex-direction: row;
  overflow-x: auto; }

.nav-list::-webkit-scrollbar {
  width: 0; }

ul,
li {
  margin: 0;
  padding: 0;
  text-decoration: none; }

ul {
  margin: 24px 0 0 16px; }

.nav-list__item {
  display: inline-flex;
  box-sizing: border-box;
  border-radius: 5px;
  height: 32px;
  margin: 0 5px 0 0;
  border: 1px solid #F8F8F8; }

.nav-list__item a {
  display: inline-block;
  font-size: 18px;
  padding: 3px 7px;
  margin: 0 0px;
  text-decoration: none;
  color: #7E7E82;
  height: 24px;
  text-align: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content; }

.nav-list__item:hover {
  background-color: white;
  border-color: #B8FFEC; }

.block1 h2 {
  font-family: 'TTLakes', sans-serif;
  font-weight: normal;
  margin-left: 10px;
  color: #7E7E82; }

.sectext {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 0px; }

.text-text__articl {
  display: block;
  float: left;
  font-size: 14px;
  height: 90px;
  margin: 16px 0 16px 16px;
  color: #1b1c21;
  overflow: hidden; }

.text-text__link {
  text-decoration: none;
  font-family: 'TTLakes', sans-serif;
  font-weight: mid;
  font-size: 16px;
  color: #1b1c21;
  margin: 0 0 0 16px; }

.text-text__link img {
  vertical-align: middle; }

.text-text__link span {
  height: 24px;
  width: 24px;
  font-family: 'TTLakes', sans-serif;
  font-weight: bold;
  font-size: 16px;
  color: #1b1c21; }

.text-img img {
  display: block;
  margin: 16px 0 24px 0;
  padding: 0;
  width: 100%;
  height: 100%; }

@media screen and (min-width: 1121px) {
  .line {
    display: none; }
  .nav-list {
    flex-wrap: wrap;
    justify-content: flex-start; } }

@media screen and (min-width: 768px) and (max-width: 1120px) {
  .main-line {
    display: none; }
  /*.nav-list li:first-child,
	.nav-list li:nth-child(5) {
		display: none;
	}*/
  .text-text__articl {
    height: 180px; } }

@media screen and (max-width: 768px) {
  /*heder*/
  .scrin__360,
  .line__der__burger {
    display: none; }
  .sectext {
    grid-template-columns: 1fr; }
  .main-line {
    display: none; }
  /*.nav-list li:first-child,
	.nav-list li:nth-child(5) {
		display: none;
	}*/
  section .back_menu {
    display: block; } }
